@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/oswaldregular.woff2") format("woff2"), url("../../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
::placeholder,
.selection-form__text,
.selection-form__textarea {
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  ::placeholder,
  .selection-form__text,
  .selection-form__textarea {
    font-size: 20px;
    line-height: 30px;
  }
}
.selection-form__text:invalid:focus {
  box-shadow: 0 0 0 1px #FF8282;
  border: 1px solid #FF8282;
  outline: none;
}
.selection-form__text:invalid:focus:hover,
.selection-form__text:invalid:focus:focus {
  box-shadow: 0 0 0 2px #FF8282;
}
.display-none {
  display: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.decorative-header {
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 300;
  margin-top: 0;
  position: relative;
  display: inline-flex;
}
@media (min-width: 768px) {
  .decorative-header {
    font-weight: 300;
    font-size: 36px;
    line-height: 36px;
    padding-left: 38px;
    padding-right: 38px;
  }
}
.decorative-header::before,
.decorative-header::after {
  content: "";
  position: absolute;
  width: 100vw;
  height: 2px;
  top: 22px;
}
.decorative-header::before {
  background-color: #68B738;
  display: none;
}
@media (min-width: 768px) {
  .decorative-header::before {
    display: block;
    left: 0;
    transform: translateX(-100%);
  }
}
@media (min-width: 1440px) {
  .decorative-header::before {
    left: -33px;
  }
}
.decorative-header::after {
  top: 15px;
  transform: translateX(100%);
  background-color: #68B738;
  right: -22px;
}
@media (min-width: 768px) {
  .decorative-header::after {
    top: 22px;
    right: 3px;
  }
}
@media (min-width: 1440px) {
  .decorative-header::after {
    right: -38px;
  }
}
