.main-nav-toggle {
  width: 24px;
  height: 16px;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0;
  background-color: #ffffff;
  outline: none;
}
.main-nav-toggle.hidden {
  display: none;
}
.main-nav-toggle.active .main-nav-toggle__line {
  opacity: 1;
  transform: rotate(-45deg);
  top: 8px;
}
.main-nav-toggle.active .main-nav-toggle__line:nth-of-type(2) {
  opacity: 0;
  transform: translateX(16px);
  top: 8px;
}
.main-nav-toggle.active .main-nav-toggle__line:nth-of-type(3) {
  opacity: 1;
  top: 8px;
  transform: rotate(45deg);
}
@media (min-width: 768px) {
  .main-nav-toggle {
    display: none;
  }
}
.main-nav-toggle__line {
  transition: all 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  height: 1.5px;
  width: 100%;
  background-color: #000000;
}
.main-nav-toggle__line:nth-of-type(2) {
  top: 8px;
}
.main-nav-toggle__line:nth-of-type(3) {
  top: 16px;
}
