.copyright-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 22px 0 22px 0;

  text-decoration: none;

  svg {
    fill: @special-dark-grey-2;
  }

  &:hover svg {
    fill: @basic-green;
  }

  &:active {
    opacity: 0.5;
  }

  @media (min-width: @tablet) {
    padding: 0;
  }
}

.copyright-link__text {
  font-size: 16px;
  line-height: 18px;
  font-family: @arial;
  color: @special-dark-grey;

  pointer-events: all;

  @media (min-width: @tablet) {
    margin-right: 22px;

    pointer-events: auto;
  }
}

.copyright-link__img {
  pointer-events: all;
  width: 27px;
  height: 34px;

  @media (min-width: @tablet) {
    pointer-events: auto;
  }
}
